//美化select元素
base.ui.fakeSelect = (function(){
    function fakeSelect(option){
        if(!$(option.elem).is('select')){
            console.error('fakeSlect函数只能用在select元素上');
            return;
        }

        this.option = $.extend({
            icon: '<i class="icon icon-user"></i>',
            placeholder: '选择一项'
        }, option);
    }

    //初始化
    fakeSelect.prototype.init = function(){
        var that = this;

        //初始化元素为jQuery对象
        this.$elem = $(this.option.elem).val('').data('changed', false);
        //创建容器
        this.$box = $('<div class="fakeselect"></div>').html('');
        //创建图标
        this.$icon = $(this.option.icon).addClass('fakeselect-icon').appendTo(this.$box);
        //创建列表头
        this.$head = $('<div class="fakeselect-head"></div>').html(this.option.placeholder).appendTo(this.$box);
        this.$head.on('click', function(event){
            //event.preventDefault();
            if(that.$menu.is(':visible')) {
                that.$menu.fadeOut(200);
                $(this).removeClass('active');
            } else {
                that.$menu.fadeIn(200);
                $(this).addClass('active');
            }

        });
        //创建菜单
        this.$menu = $('<ul class="fakeselect-option"></ul>').appendTo(this.$box);
        $.each(this.$elem.find('option'), function(){
            var item = $('<li class="fakeselect-item" data-val="'+$(this).val()+'">'+$(this).html()+'</li>');
            item.appendTo(that.$menu);
            //菜单项点击事件改变原<select>的值
            item.on('click', function(event){
                //event.preventDefault();
                that.$elem.val(item.data('val'));
                that.$head.html(item.html());
                that.$menu.fadeOut(200);
                that.$head.removeClass('active');
                that.$elem.data('changed', true);
            });
        });
        //空白处点击隐藏菜单
        $(document).on('click', function(e){
            var flag = false;
            $.each($(e.target).parents(), function(){
                if(this == that.$box.get(0)){
                    flag = true;
                }
            });
            if(!flag){
                that.$menu.fadeOut(200);
                that.$head.removeClass('active');
            }
        });

        this.$elem.after(this.$box).hide(0);
    };

    fakeSelect.prototype.destroy = function(){
        this.$box.remove();
        this.$elem.show(0);
    };

    return fakeSelect;
})();