;base.form.isPhoneNum = function(str){
    if((typeof str).toLowerCase() !== 'string'){
        return {
            success: false,
            msg: '参数错误'
        };
    }

    //不可为空
    if(str.length <= 0){
        return {
            success: false,
            msg: '请填写手机号'
        };
    }

    if(!/^1[3|4|5|7|8]\d{9}$/.test(str)){
        return {
            success: false,
            msg: '手机号有误'
        };
    }

    return {
        success: true,
        msg: ''
    };
};