/*
 * “加载中”遮罩效果，依赖 base.ui.mask
 * 调用示例：
 * base.ui.loading('show', '提示文字');  //显示加载遮罩
 * base.ui.loading('hide');  //隐藏加载遮罩
 */
base.ui.loading = (function(){
    "use strict";
    var timer = null, index = 0;

    //容器
    var $box = $('<div></div>')
        .css({
            zIndex: 2147483647,
            position: 'fixed',
            top: $(window).height() / 2 - 40,
            left: $(window).width() / 2 - 100,
            width: '200px',
            height: '80px'
        });

    //提示的文字
    var $showText = $('<div></div>')
        .css({
            color: '#ffffff',
            textAlign: 'center',
            fontSize: '24px',
            fontWeight: 'bold'
        })
        .appendTo($box);

    //滚动圆点
    var $loading = $('<ul><li></li><li></li><li></li><li></li><li></li></ul>')
        .css({
            zIndex: 2147483647,
            listStyle: 'none',
            position: 'absolute',
            bottom: 0,
            width: '200px',
            height: '20px',
            margin: 0,
            padding: 0
        })
        .appendTo($box);
    var $dot = $loading.find('li').css({
        float: 'left',
        width: '20px',
        height: '20px',
        margin: '0 10px',
        borderRadius: '50%',
        backgroundColor: '#ffffff',
        transition: 'transform .2s'
    });

    function render(action, text){
        $box.css({
            top: $(window).height() / 2 - 40,
            left: $(window).width() / 2 - 100
        });
        $showText.html(text || ' ');
        if('hide' != action) {
            clearInterval(timer);
            base.ui.mask('show');
            $box.appendTo('body').fadeIn(200);
            timer = setInterval(function(){
                $dot.css({
                    transform: 'scale(1)'
                })
                    .eq(index)
                    .css({
                        transform: 'scale(1.5)'
                    });
                index + 1 >= $dot.length ? index = 0 : index++;
            }, 250);
        } else {
            clearInterval(timer);
            base.ui.mask('hide');
            $box.fadeOut(200);
        }
    }

    return render;
})();