/*
* 登录接口
* base.action.login({
*   mobile: 手机号
*   password: 密码
* }, function(){回调});
* */
;base.action.login = function(option){
    var opt = $.extend({
        mobile: false,
        password: false,
        callback: function(data){},
        beforeSend: function(){},
        complete: function(){}
    }, option);

    if(!opt.mobile || !opt.password){
        console.log('参数错误');
        return false;
    }

    $.ajax({
        url: '/member/loginsubmit',
        type: 'POST',
        data: {
            mobile: opt.mobile,
            password: opt.password
        },
        dataType: 'json',
        timeout: 20000,
        beforeSend: opt.beforeSend,
        success: function(data){
            opt.callback(data);
        },
        complete: opt.complete,
        error: function(){
            base.ui.alert('网络错误');
        }
    });
};