;base.action.sendRegSms = function(option){
    option = $.extend({
        mobile: false,
        verify: false,
        callback: function(data){},
        beforeSend: function(){},
        complete: function(){}
    }, option);

    if(!option.mobile || !option.verify){
        base.ui.alert('参数错误');
        return false;
    }

    $.ajax({
        url: '/public/sendverifysms.html',
        type: 'POST',
        dataType: 'json',
        data: {
            mobile: option.mobile,
            verify: option.verify
        },
        beforeSend: option.beforeSend,
        success: option.callback,
        complete: option.complete,
        error: function(){
            base.ui.alert('网络错误');
        }
    });
};