/*
* 模态框
* base.ui.modal('#elem');
* 或
* base.ui.modal({
*   el: '#elem',
*   top: -2,
*   left: -2
* });
* */

base.ui.modal = (function(){
    var $oldContent = $('null');

    var $win = $('<div></div>').css({
        overflow: 'hidden',
        display: 'none',
        zIndex: '2147483647',
        position: 'fixed',
        width: '450px',
        maxWidth: '90%',
        minHeight: '50px',
        maxHeight: $(window).height() * 0.8,
        padding: '20px 15px',
        backgroundColor: '#ffffff',
        border: '1px solid #dddddd',
        boxShadow: '2px 2px 10px rgba(0,0,0,.15)'
    }).addClass('ui-modal');
    var $close = $('<a href="#">&times;</a>');
    $close.css({
        position: 'absolute',
        top: 0,
        right: 0,
        width: '25px',
        height: '25px',
        lineHeight: '25px',
        textAlign: 'center',
        textDecoration: 'none',
        color: '#999999',
        fontSize: '24px'
    }).hover(function(){
        $(this).css({
            color: '#000000'
        });
    }, function(){
        $(this).css({
            color: '#999999'
        });
    }).click(function(event){
        event.preventDefault();
        hide();
    });
    $close.appendTo($win);

    var $content = $('<div style="max-height:'+$(window).height()*0.8+'px;margin-top:5px;"></div>').appendTo($win);

    function hide(){
        base.ui.mask('hide');
        $content.children().hide(0).appendTo(document.body);
        $win.fadeOut(250);
    }

    function show(option){
        $oldContent.hide(0).appendTo('body');
        var $elem = null;
        if((typeof option).toLowerCase() == 'string'){
            $elem = $(option);
            base.ui.mask();
            $elem.appendTo($content).show(0);
            $win.appendTo(document.body);
            $win.fadeIn(250);
            $oldContent = $elem;
        } else {
            $elem = $(option.el);
            base.ui.mask();
            $elem.appendTo($content).show(0);
            $win.appendTo(document.body);
            $win.fadeIn(250);
            $oldContent = $elem;
        }
    }

    function modal(option){
        if('hide' == option){
            hide();
        } else {
            option = $.extend({
                top: 0,
                left: 0,
                width: 450
            }, option);
            show(option);
        }
        $win.css({
            top: $(window).height() * 0.25 + option.top < 0 ? 0 : $(window).height() * 0.25 + option.top,
            left: $(window).width() / 2 - option.width / 2 + option.left,
            width: option.width
        });
    }
    return modal;
})();