;base.form.isPassword = function(str){
    if((typeof str).toLowerCase() !== 'string'){
        return {
            success: false,
            msg: '参数错误'
        };
    }

    //不可为空
    if(str.length <= 0){
        return {
            success: false,
            msg: '请填写密码'
        };
    }

    if(str.length < 6){
        return {
            success: false,
            msg: '密码不能少于6位'
        };
    }

    return {
        success: true,
        msg: ''
    };
};