base.form.isCertificateNum = function(str){
    if((typeof str).toLowerCase() != 'string'){
        base.ui.alert('参数错误');
        return;
    }

    if(str.length <= 0){
        return {
            success: false,
            msg: '身份证不能为空'
        };
    }

    var temp = /^[1-9][0-9]{5}(19[0-9]{2}|200[0-9]|2010)(0[1-9]|1[0-2])(0[1-9]|[12][0-9]|3[01])[0-9]{3}[0-9xX]$/i.test(str);
    if(!temp){
        return {
            success: false,
            msg: '身份证有误'
        };
    }

    return {
        success: true,
        msg: ''
    }
};