;base.action.borrow = function(option){
    option = $.extend({
        borrowtype: false,
        name: false,
        mobile: false,
        smscode: false,
        certinumber: false,
        amount: false,
        borrowtime: false,
        callback: function(data){},
        beforeSend: function(){},
        complete: function(){}
    }, option);

    var checkFail = !option.borrowtype || !option.name || !option.mobile || !option.smscode || !option.certinumber || !option.amount || !option.borrowtime;
    if(checkFail){
        base.ui.alert('参数错误');
        return;
    }

    $.ajax({
        url: '/borrow/borrowadd.html',
        type: 'POST',
        dataType: 'json',
        timeout: 20000,
        data: {
            name: option.name,
            mobile: option.mobile,
            smscode: option.smscode,
            certinumber: option.certinumber,
            amount: option.amount,
            borrowtime: option.borrowtime
        },
        beforeSend: option.beforeSend,
        success: function(data){
            option.callback(data);
        },
        complete: option.complete,
        error: function(){
            base.ui.alert('网络错误');
        }
    });
};