/*
 * “遮罩”效果
 * 调用示例：
 * base.ui.mask('show');  //显示遮罩
 * base.ui.mask('hide');  //隐藏遮罩
 */
base.ui.mask = (function(){
    "use strict";
    var $mask = $('<div></div>')
        .css({
            zIndex: 2147483646,
            display: 'none',
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: '#000000',
            opacity: 0.618
        });
    function render(option){
        if('hide' != option) {
            $mask.appendTo('body').fadeIn(200);
        } else {
            $mask.fadeOut(200);
        }
    }

    return render;
})();