base.ui.boxShadow = function(option){
    "use strict";
    option = $.extend({
        elem: false,
        borderColor: '#2e9ee6',
        boxShadow: '0 0 10px rgba(46,158,230,.382)'
    }, option);
    if(!option.elem){
        console.error('elem参数未定义');
        return;
    } else {
        $(option.elem).css({
            borderColor: option.borderColor,
            boxShadow: option.boxShadow
        });
    }
};