base.action.register = function(option){
    option = $.extend({
        mobile: false,
        code: false,
        pwd: false,
        invite: '',
        callback: function(data){},
        beforeSend: function(){},
        complete: function(){}
    }, option);

    if(!option.mobile || !option.pwd || !option.code){
        console.log('参数错误');
        return false;
    }

    $.ajax({
        url: '/member/register',
        type: 'POST',
        dataType: 'json',
        data: {
            mobile: option.mobile,
            code: option.code,
            pwd: option.pwd,
            invite: option.invite
        },
        beforeSend: option.beforeSend,
        success: option.callback,
        complete: option.complete
    });
};