;base.action.sendBorrowSms = function(option){
    option = $.extend({
        mobile: false,
        callback: function(data){},
        beforeSend: function(){},
        complete: function(){}
    }, option);
    if(!option.mobile){
        base.ui.alert('参数错误');
        return;
    }

    $.ajax({
        url: '/public/borrowverifysms.html',
        type: 'POST',
        dataType: 'json',
        data: {
            mobile: option.mobile
        },
        timeout: 20000,
        beforeSend: option.beforeSend,
        success: function(data){
            option.callback(data);
        },
        complete: option.complete,
        error: function(){
            base.ui.alert('网络错误');
        }
    });
};